import React from 'react';
import './static/mini-default.min.css';
import './static/App.css';
import Lib from './code'
import Header from './code/header';
import HelmetComponent from './code/helmet';

export class App extends React.Component {

    state = {
        cases: [],
        closest: null,
        loading: false,
        error: null,
        geolocationPermission: null
    }

    constructor(props) {
        super(props);
        this.loadData = this.loadData.bind(this);
        this.loadGeoPermissionStatus = this.loadGeoPermissionStatus.bind(this);
        /*
        this.loadData()
        .catch((err) => {
            console.log({ err });
        })
        */
       this.loadGeoPermissionStatus()
       .then(()=>{
            console.log("--- 1");
            this.loadData();
       })
       .catch((err)=>{
        console.log("--- 2");
        console.log({err});
       });
    }

    async loadGeoPermissionStatus(){
        try{
            let geolocationPermission = await Lib.instance.hasGeoLocationActivated();
            this.setState({geolocationPermission})
        }catch(err){
            console.log({err});
            this.setState({geolocationPermission: null})
        }
    }

    async loadData() {
        let lib = new Lib();
        //let cases = await lib.fetchData();
        //let location = await lib.fetchLocation();
        this.setState({
            loading: true
        });
        try {
            const _res = await Promise.all([lib.fetchData(), lib.fetchLocation()])
            let cases = _res[0];
            let location = _res[1];
            cases = cases.map((element) => {
                let raw_distance = lib.distance(location.coords[0], location.coords[1], element.latitude, element.longitude, 'K');
                element.distance_from_user = Math.round(raw_distance * 100) / 100;
                return element;
            })
            .sort((elementA, elementB) => {
                return (elementA.distance_from_user > elementB.distance_from_user) ? 1 : (elementA.distance_from_user < elementB.distance_from_user) ? -1 : 0;
            });
            //Lib.instance
            this.setState({
                cases: cases,
                closest: cases[0],
                loading: false
            });
        } catch (e) {
            console.log({e});
            this.setState({
                loading: false, error: e
            })
        }
    }

    getAuthorizedScreen() {

        let elements = this.state.cases
            .filter((e, index) => index > 1)
            .map((element1, index) =>
                <tr key={`LAT=${element1.latitude};LONG=${element1.longitude};${index}`}>
                    <td data-label="Location">{element1.country_region} <small>{element1.province_state}</small></td>
                    <td data-label="Distance Away">{element1.distance_from_user} km</td>
                    <td data-label="Confirmed">{element1.confirmed}</td>
                    <td data-label="Deaths">{element1.deaths}</td>
                    <td data-label="Recovered">{element1.recovered}</td>
                </tr>
            )
        let closest = "";
        if (this.state.closest) {
            closest =
                <div className="card error" style={{ maxWidth: '100%' }}>
                    <h2>{this.state.closest.distance_from_user} km <small style={{ color: 'white' }}>{this.state.closest.country_region} {this.state.closest.province_state ? '-' + this.state.closest.province_state : ''}</small></h2>
                    <div>
                        <p>Confirmed: {this.state.closest.confirmed}</p>
                        <p>Deaths: {this.state.closest.deaths}</p>
                        <p>Recovered: {this.state.closest.recovered}</p>
                    </div>
                </div>
        }
        return (<div className="App">
            <Header />
            <div className="container">
                <div className="row">
                    <div className="col-sm-12">
                        <h1>CC: CoronaClose
                                <small>How close are you from the <span style={{ color: 'red' }}>Corona VID-19</span> cases?</small>
                        </h1>
                        <h2>Closest case to you (at {this.state.closest.report_date})</h2>
                        <small><a href="https://github.com/CSSEGISandData/COVID-19">Information Source</a></small>
                    </div>
                </div>
                <div className="row">
                    {closest ? closest : ''}
                </div>
                <div className="row">
                    <div className="col-sm-12 col-md-12 col-sm-first">
                        <h3>Next Closest Cases</h3>
                        <table>
                            <thead>
                                <tr>
                                    <th>Location</th>
                                    <th>Distance</th>
                                    <th>Confirmed</th>
                                    <th>Deaths</th>
                                    <th>Recovered</th>
                                </tr>
                            </thead>
                            <tbody>
                                {elements}
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        </div>);
    }

    getNotLoadedScreen() {
        return (
            <div className="row" style={{ justifyContent: 'center' }}>
                <div className="card" style={{ textAlign: 'center' }}>
                    <h1>Note: You must enable location in order to get the data</h1>
                    <button className="button" onClick={this.loadData}>Begin</button>
                </div>
            </div>
        )
    }

    getLoadingScreen() {
        return (
            <div className="row" style={{ justifyContent: 'center' }}>
                <div className="col-sm-12">
                    <h1>Loading...</h1>
                </div>
            </div>
        )
    }

    getErrorScreen(){
        return (
            <div className="row" style={{ justifyContent: 'center' }}>
                <div className="col-sm-12">
                    <h1>An error has occurred. Please try again later and try cleaning your browser cache.</h1>
                    <button className="button" onClick={this.loadData}>Try Again</button>
                </div>
            </div>
        )
    }

    render() {
        if(this.state.closest){
            return <div>
                <HelmetComponent/>
                {this.getAuthorizedScreen()}
            </div>;
        }
        if(this.state.loading){
            return <div>
                <HelmetComponent/>
            {this.getLoadingScreen()}</div>
        }
        if(this.state.error){
            return <div>
                <HelmetComponent/>
            {this.getErrorScreen()}</div>
        }
        return <div>
        <HelmetComponent/>
        {this.getLoadingScreen()}</div>;
    }
}

export default App;
