import React from 'react';
import {Helmet} from 'react-helmet';

export default class Header extends React.Component {
    constructor(props){
        super(props);
    }

    shareOnFb(){
        window.open('https://www.facebook.com/sharer/sharer.php?u='+encodeURIComponent(window.location.href), '_blank');
    }

    getTwitterShareURL(){
        return "https://twitter.com/intent/tweet?text="+encodeURIComponent("How close are you from the CoronaVirus reported cases? Get the answer on: "+window.location.href);
    }

    getShareWhatsappLink(){
        const href = "whatsapp://send?text=How close are you from the CoronaVirus reported cases? Get the answer on: "+window.location.href;
        return <a href={href} data-action="share/whatsapp/share" className="button" style={{backgroundColor:'#1EBEA5',color:'white', fontWeight:'bold'}}>Whatsapp</a>
    }

    render() {
        return (
            <header className="sticky">
                <a href="./" className="logo"><b>CClose</b></a>
                <label htmlFor="modal-control" className="button">Share</label>
                <a href="mailto:azolotdev@gmail.com" className="button">Contact</a>
                <input type="checkbox" id="modal-control" className="modal" />
                <div>
                    <div className="card">
                        <label htmlFor="modal-control" className="modal-close" ></label>
                        <h3 className="section" style={{paddingBottom:'1em'}}>Share via</h3>
                        <a className="button" href={this.getTwitterShareURL()} style={{backgroundColor:'#1DA1F2', color:'white', fontWeight:'bold'}}>Twitter</a>
                        {this.getShareWhatsappLink()}
                        <button className="button" onClick={this.shareOnFb} style={{backgroundColor:'#3C5A9A', color:'white', fontWeight:'bold'}}>Facebook</button>
                    </div>
                </div>
            </header>
        )
    }
}