import Dexie from 'dexie';
export class DBManager {

    static get instance(){
        window['_db_man'] = window['_db_man'] ? window['_db_man'] : new DBManager();
        return window['_db_man'];
    }

    get db(){
        if(!DBManager.instance['db_connection']){
            DBManager.instance['db_connection'] = new Dexie("covidDatabase");
            DBManager.instance['db_connection'].version(1).stores(
            {   
                latestData: "&link, date, values"
            });
        }
        return DBManager.instance['db_connection'];
    }

    async getWithKey(table, selectorFunction){
        const data = await new Promise((accept, reject)=>{
            this.db.transaction('r', this.db[table], async()=>{
                try{
                    const results = await selectorFunction(this.db[table]);
                    accept(results);
                }catch(err){
                    reject(err);
                }
            });
        });
        return data;
    }

    async storeWithKey(table, value){
        const index = await new Promise((accept, reject)=>{
            this.db.transaction('rw', this.db[table], async()=>{
                try{
                    const id = await this.db[table].add(value);
                    accept(id);
                }catch(e){
                    reject(e);
                }
            });
        });
        return index;
    }

    async deleteRecords(table, selectorFunction){
        const data = await new Promise((accept, reject)=>{
            this.db.transaction('rw', this.db[table], async()=>{
                try{
                    const results = await selectorFunction(this.db[table]);
                    accept(results);
                }catch(err){
                    reject(err);
                }
            });
        });
        return data;
    }


}