import * as Papa from 'papaparse';
import * as dayjs from 'dayjs';
import { DBManager } from './db';
import example_results from '../example_parses_result_data.json';

//https://raw.githubusercontent.com/CSSEGISandData/COVID-19/master/csse_covid_19_data/csse_covid_19_daily_reports/03-02-2020.csv

export default class Lib {


    get FILES_LIST_URL(){
        return `https://api.github.com/repos/CSSEGISandData/COVID-19/contents/csse_covid_19_data/csse_covid_19_daily_reports`;
    }

    get DEFAULT_FILE_URL(){
        let file_date = dayjs().subtract(1, 'd').format("MM-DD-YYYY") || '03-06-2020';
        return `https://raw.githubusercontent.com/CSSEGISandData/COVID-19/master/csse_covid_19_data/csse_covid_19_daily_reports/${file_date}.csv`;
    }

    async getLatestFile(){
        let dd = await fetch(this.FILES_LIST_URL);
        let files_list = JSON.parse(await dd.text());
        let _last_file = {
            download_url: ""
        };
        for(let i=files_list.length-1;i>0;i--){
            if(files_list[i].name.indexOf('csv')>-1){
                _last_file = files_list[i];
                break;
            }
        }
        return _last_file.download_url;
    }

    static get instance(){
        return window['geo_tool_1'] ? window['geo_tool_1'] : (()=>{
            window['geo_tool_1'] = new Lib();
            return window['geo_tool_1'];
        })();
    }

    distance(UserLat, userLong, caseLat, caseLong, unit) {
        if ((UserLat == caseLat) && (userLong == caseLong)) {
            return 0;
        }
        else {
            var radUserLat = Math.PI * UserLat / 180;
            var radcaseLat = Math.PI * caseLat / 180;
            var theta = userLong - caseLong;
            var radtheta = Math.PI * theta / 180;
            var dist = Math.sin(radUserLat) * Math.sin(radcaseLat) + Math.cos(radUserLat) * Math.cos(radcaseLat) * Math.cos(radtheta);
            if (dist > 1) {
                dist = 1;
            }
            dist = Math.acos(dist);
            dist = dist * 180 / Math.PI;
            dist = dist * 60 * 1.1515;
            if (unit == "K") { dist = dist * 1.609344 }
            if (unit == "N") { dist = dist * 0.8684 }
            return dist;
        }
    }

    async hasGeoLocationActivated(){
        if(navigator.permissions){
            return await new Promise((accept)=>{
                navigator.permissions.query({name: 'geolocation'}).then(function(PermissionStatus) {
                    accept(PermissionStatus.state == 'granted');
                })
            });
        }
        return null;
    }

    async fetchDeviceLocation(){
        let res = await new Promise((accept, reject) => {
            /**
             *  latitude: 10.4963315
                longitude: -66.877371
                altitude: null
                accuracy: 26
                altitudeAccuracy: null
                heading: null
                speed: null
             */
            navigator.geolocation.getCurrentPosition(accept, reject);
        });
        return {
            coords: [res.coords.latitude, res.coords.longitude],
            accuracy: res.coords.accuracy
        };
    }

    async fetchIpLocation(){
        let data = await fetch('http://free.ipwhois.io/json/');
        data = await data.json();
        return {
            coords: [data['latitude'], data['longitude']],
            accuracy: -1
        };
    }

    async fetchLocation() {
        let res;
        if ("geolocation" in navigator) {
            /* la geolocalizaci칩n est치 disponible */
            try{
                res = await this.fetchDeviceLocation();
                return res;
            }catch(e){
                console.log("Error getting device geolocation: ",{e});
            }
        }
        /* la geolocalizaci칩n NO est치 disponible o ha habido un error al obetenerla */
        //window.alert("Gelocation not available for this device");
        res = await this.fetchIpLocation();
        return res;
    }

    /**
     * @description Gets the currently stored data.
     * If no current day data is stored,
     * it returns null.
     */
    async getStoredData(downloadUrl){
        try{
            let data = await DBManager.instance.getWithKey('latestData', (latestData)=>{
                if(downloadUrl){
                    return latestData.get(downloadUrl);
                }else{
                    return latestData.orderBy('date').reverse().toArray();
                }
            });
            return downloadUrl ? data : data[0];
        }catch(e){
            console.log({e});
            return null;
        }
    }

    get currentDayMillis(){
        //03-06-2020
        return new Date(dayjs().format("MM/DD/YYYY")).getTime();
    }

    /**
     * @description This stores the fetched data into the IndexDB.
     * @param {*} data The data to be stored
     */
    async storeFetchedData(latest_link, latest_report, date_millis){
        try{
            //We purge previous records.
            await DBManager.instance.deleteRecords('latestData', function(latestData){
                return latestData.where('date').aboveOrEqual(0).delete();
            });
            //We store the new records
            let link_id = await DBManager.instance.storeWithKey('latestData', {
                date: date_millis,
                link: latest_link,
                values: latest_report
            });
            return {link_id};
        }catch(e){
            console.log({e});
            return null;
        }
    }

    async fetchData() {
        let data = await new Promise(async (accept) => {
            let download_url;
            try{
                download_url = await this.getLatestFile();
            }catch(e){
                download_url = this.DEFAULT_FILE_URL;
            }
            const currentData = await this.getStoredData(download_url);
            if(!currentData){
                let report_date = download_url.split("/").pop().split(".")[0];
                report_date = dayjs(report_date).toDate();
                Papa.parse(download_url, {
                    download: true,
                    complete: async (results) => {
                        const returnResults = results.data.map((element, index) => {
                            if (index < 1) return null;
                            let return_element =  {
                                province_state: element[0],
                                country_region: element[1],
                                last_update: element[2],
                                confirmed: parseInt(element[3]),
                                deaths: parseInt(element[4]),
                                recovered: parseInt(element[5]),
                                latitude: parseFloat(element[6]),
                                longitude: parseFloat(element[7]),
                                report_date: report_date.toLocaleDateString()
                            };
                            if(return_element.latitude != return_element.latitude) return null;
                            return return_element;
                        }).filter((element) => !!element);
                        await this.storeFetchedData(download_url, returnResults, report_date.getTime());
                        accept(returnResults);
                    }
                });
            }else{
                accept(currentData.values);
            }
        });
        return data;
    }
}