import React from 'react';
import {Helmet} from 'react-helmet';

export default class HelmetComponent extends React.Component {
    constructor(props){
        super(props);
    }

    render(){
        return (
            <Helmet>
                <meta charSet="utf-8" />
                <title>CClose: Closes CoronaVirus case</title>
                <link rel="canonical" href="./" />
                <link rel="icon" type="image/png" href="./virus_icon.png" sizes="16x16" />
                <meta name="description" content="Closes Coronavirus to your location. Daily report"></meta>
            </Helmet>
        );
    }
}